<template>
    <div class="about-cart">
        <div class="purchase-content">
            <div :class="{'purchase':true,'active':len}">
                <div class="cart" @touchend="showCartHandle">
                    <i class="el-icon-shopping-cart-2"></i>
                    <span class="goods-num" v-if="len">{{len}}</span>
                </div>
                <div class="pri">￥{{sumPrice}}</div>
                <div class="peisong">另需配送费￥4元</div>
            </div>
            <div :class="{'total-price':true,'active':len}" >
                <span v-if="len == 0">￥20起送</span>
                <span v-if="len">结算</span>
            </div>
        </div>
         <div class="cart-container" v-if="showCart" @touchend="closeCart">
            <div class="cart-content" @touchend.stop="notCloseCart">
                <dl>
                    <dt class="cart-title">
                        <span>购物车</span>
                        <span class="clear" @touchend.stop="clearCart">清空</span>
                    </dt>
                    <dd v-for="item in showCartList">
                        <div class="cart-item">
                            <span class="goods">{{item.name}}</span>
                            <div class="price-num">
                                <span class="per-price"><span>￥</span>{{item.price}}</span>
                                <add-minus :food-item="item" @isShowCart="isShowCart"></add-minus>
                            </div>
                        </div>  
                    </dd> 
                </dl>
            </div>
        </div>
    </div>
     
</template>

<script>
import {mapState, mapMutations} from 'vuex'
import AddMinus from '@/components/public/AddMinus'
export default{
    components:{
        AddMinus
    },
    data(){
        return {
            showCart:false,
            len:0,
            sumPrice:0,
            showCartList:[],
            saveCartList:[]
        }
    },
    methods:{
        showCartHandle(){
            if(this.len){
                this.showCart = !this.showCart;
            } 
        },
        isShowCart(val){
            this.showCart = val;
        },
        clearCart(){
            const flag = window.confirm('确定要清空购物车？');
            if(flag){
                this.showCartList = [];
                this.saveCartList = [];
                this.changeCartContent(this.saveCartList);
                this.showCart = false;
            }
        },
        initAndObserver(){
            this.len = this.cartList.length;
            this.cartList.forEach(item => this.sumPrice += item.price);
            this.showCartList = this.saveCartList = [].concat(this.cartList);
            this.showCartList = new Set(this.showCartList.map(item=>JSON.stringify(item)));
            this.showCartList = [...this.showCartList];
            this.showCartList = this.showCartList.map(item => JSON.parse(item));
        },
        closeCart(){
            this.showCart = false;
        },
        notCloseCart(){
            this.showCart = true;
        },
        ...mapMutations(['changeCartContent'])
    },
    mounted(){
        this.initAndObserver();
    },
    computed:{
        ...mapState(['cartList'])
    },
    watch:{
        cartList(val){
            this.sumPrice = 0;
            this.initAndObserver();
        }
    }
}
</script>

<style scoped lang="scss">
    @import "@/assets/css/public/elemFooter.scss"
</style>